 y avanza hasta invadir totalmente, acentuándose cuando el producto se fracciona debido a una mayor área de exposición.")])]),_c('div',{staticClass:"tarjeta cftpc2 p-3",attrs:{"x":"50%","y":"77%","numero":""}},[_c('p',{staticClass:"mb-0"},[_vm._v("El tono claro vuelve al verdoso, que había sido eliminado por el Na2S2O4, debido al paso del complejo ferroso a férri­co por oxidación al con­tacto con el aire. Así mismo, aún en panela sin aditivos químicos se presenta oscurecimiento del producto por acción de la luz.")])]),_c('div',{staticClass:"tarjeta cftpc1 p-3",attrs:{"x":"86%","y":"77%","numero":""}},[_c('p',{staticClass:"mb-0"},[_vm._v("Sobre la superficie se presenta invasión de hongos, levaduras y bacterias.")])])])],1)]),_vm._m(11),_c('p',[_vm._v("El 28 de septiembre de 2011 fue el último día para que la industria panelera colombiana comenzará a cumplir con los requisitos establecidos en el Decreto 779, del 17 de marzo de 2006, así como la información contenida en el Decreto 4121, del 16 de septiembre de 2011, emitido por el Departamento de Seguridad Social.")]),_c('p',{staticClass:"mb-4"},[_vm._v("A continuación, se presentan algunas de las precisiones que deben ser contempladas para el empaque y embalaje de la panela en Colombia.")]),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--gris1"}},[_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Normatividad de empacado y embalaje"}},[_c('div',{staticClass:"col-md-11"},[_c('p',[_vm._v("Durante el proceso documentado de fabricación y comercialización, las panelas deben cumplir con los estándares de empaque y embalaje relacionados con avalar la calidad y seguridad de las etiquetas con la garantía de calidad e inocuidad que deben brindar los materiales primarios, secundarios y de las etiquetas de la panela en bloque.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo"