        img(src="@/assets/curso/temas/tema3/img3-1.png", alt="")
      .col-lg-8(data-aos="fade-left")
        p La panela es un producto higroscópico, es decir que absorbe o pierde humedad bajo la influencia de las condiciones ambientales, dependiendo de las condiciones climáticas y de las características del producto.
        p La panela tiende a cambiar cuando existe una combinación de azúcar bajo, sacarosa baja y humedad alta. A medida que aumenta el contenido de humedad, la panela se ablanda, cambia de color, aumenta el contenido de azúcar reductor y reduce el contenido de sacarosa; en estas condiciones, puede infectarse fácilmente con parásitos. Si la panela preparada tiene un contenido de humedad entre 7 % y 10 %, debe transportarse, distribuirse y consumirse rápidamente, ya que el almacenamiento a largo plazo destruye su calidad.  A partir del 10 % de humedad, la superficie brilla dado que aparecen pequeñas gotas de melaza; no se puede almacenar en tales condiciones debido al riesgo de invasión microbiana y cambios fisicoquímicos.
        p.mb-0 Los materiales plásticos termo encogibles y las láminas de aluminio plastificado, son aptos para almacenar la panela durante un largo periodo de tiempo ya que no cambian sus propiedades organolépticas. Este concepto hace referencia al número de propiedades físicas de todos los materiales, que los sentidos pueden percibir como, por ejemplo, su sabor, forma, olor, color o temperatura.
    
    .row.justify-content-center.mb-5
