          .tarjeta.cftpc1.p-3(x="20%" y="8%" numero="")
            p.mb-0 Apilar ordenadamente o en tarimas con un buen espacio libre entre paredes y pisos.
          .tarjeta.cftpc1.p-3(x="9%" y="34%" numero="")
            p.mb-0 El almacenamiento debe realizarse en condiciones apropiadas de temperatura, humedad y circulación de aire.
          .tarjeta.cftpc1.p-3(x="9%" y="59%" numero="")
            p.mb-0 Debe llevarse un control de primeras entradas y salidas con el fin de garantizar la rotación de los productos. A veces, las empresas necesitan deshacerse de productos y equipos obsoletos e inutilizables para ayudar a limpiar los nuevos y así eliminar la posible contaminación.
          .tarjeta.cftpc1.p-3(x="20%" y="84%" numero="")
            p.mb-0 Los materiales y productos terminados se almacenan de manera que se minimicen los daños y se protejan contra condiciones que puedan afectar su limpieza, función e integridad. Además, deben estar claramente identificados para comprender su origen, calidad y vida útil.
          .tarjeta.cftpc2.p-3(x="76%" y="8%" numero="")
